import React from 'react';
import './Home.css';

import logo from '../img/logo.png';

var coin = 0;

class Home extends React.Component {
  state = {
    text: '0'
  }

  onClickButton1 = () => {
    coin += 1;
    this.setState({
      text: `${coin}`
  });
  }
  render() {
    return (
     <div>
        <h1>Coins</h1>
        <p>Баланс: {this.state.text}</p>
        <button onClick={this.onClickButton1} id="click"><img src={logo} alt="Logo"/></button>     </div>
    );
  }
}

export default Home;
